.invalidPage {
	font-size: 6vw;
	font-weight: 500;
	word-break: normal;
	max-width: 90%;
	margin: 30% auto auto auto;
}

.Download {
	/* display: flex; */
	flex-direction: column;
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: calc(100% - 65px);
	background-color: #171722;
	z-index: 7;
	overflow: hidden;
}
.Download .back {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	position: absolute;
	top: 10px;
	left: calc(50% - 170px / 2);
	height: 40px;
	width: 170px;
	background-color: #24242d;
	border-radius: 8px;
	padding: 0 20px;
	box-shadow: 0 0 0 2px #171722;
	cursor: pointer;
}
.Download .back * {
	margin-left: -10px;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.Download .back svg {
	height: 60%;
	aspect-ratio: 1/1;
}
.Download main {
	margin-top: 30px;
	overflow-y: scroll;
	overflow-x: hidden;
	margin-right: -10px;
	padding-right: 10px;
}
.Download main * {
	font-size: 24px;
	font-weight: 300;
}
.Download main img {
	background-color: #555555;
	width: 100%;
}
.Download main img.header {
	height: 110px;
}
.Download main h1 {
	font-size: 40px;
	font-weight: 500;
}
.Download main img.youtube {
	margin-top: 20px;
	height: 170px;
	aspect-ratio: 17/10;
	border-radius: 8px;
}
.Download main .media-list {
	margin-top: 50px;
	flex-direction: column;
}
.Download main .media-list .card {
	margin-top: 10px;
	flex-direction: column;
	width: 100%;
}
.Download main .media-list .card img {
	height: 230px;
}
.Download main summary {
	margin-top: 20px;
	cursor: pointer;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.Download .downloads {
	display: flex;
	flex-direction: column;
	text-align: center;
}
.Download .downloads .download {
	display: inline-flex;
	flex-direction: column;
	align-items: center;
	background-color: #2f4f4f;
	border-radius: 8px;
	padding: 4px 40px;
	margin: 40px auto;
	cursor: pointer;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.Download .downloads .download * {
	margin-top: -2px;
}
.Download .downloads .download p {
	font-weight: 400;
}
.Download .downloads .download h6 {
	margin-top: -3px;
	font-weight: 400;
	font-size: 10px;
}
